/**
 * Decimal
 *
 * Extends the decimal.js Decimal object to keep track of the original
 * number of decimal points at construction so that toString as well
 * as any operations output in the same fashion as Python's Decimal class
 *
 * @author Chris Nasr
 * @copyright OuroborosCoding
 * @created 2019-03-03
 */

// Import modules
import BaseDecimal from 'decimal.js';

// Import helpers
import is from './is';
import types from './types';

// Decimal class
export default class Decimal extends BaseDecimal {

	/**
	 * Constructor
	 *
	 * Initialises the instance
	 *
	 * @name Decimal
	 * @access public
	 * @param mixed val				A value to convert to a decimal
	 * @return Decimal
	 */
	constructor(v) {

		// Call the parent constructor
		super(v);

		// Assume no decimal points
		this.points = 0;

		// If the original value is a string
		if(is.string(v)) {

			// Attempt to get the fraction
			var a = types.regex.decimal.exec(v);

			// If it exists and there's a trailing 0
			if(a && a[1]) {
				this.points = a[1].length;
			}
		}
	}

	/**
	 * From decimal.js
	 *
	 * Convert a decimal.js value into our class
	 *
	 * @name _fromDecimalJS
	 * @access public
	 * @static
	 * @param BaseDecimal v			The base class instance
	 * @param uint[] p				Optional points
	 * @return Decimal
	 */
	static _fromDecimalJS(v, p) {

		// Generate a new instance
		var d = new Decimal(v);

		// Copy over the points length
		d.points = p;

		// Return the instance
		return d;
	}

	/**
	 * Absolute
	 *
	 * Returns the decimal as an absolute value
	 *
	 * @name abs
	 * @access public
	 * @return Decimal
	 */
	abs() {

		// Call the parent method and convert it
		return Decimal._fromDecimalJS(
			super.abs(),
			this.points
		);
	}

	/**
	 * Absolute Value
	 *
	 * Returns the decimal as an absolute value
	 *
	 * @name absoluteValue
	 * @access public
	 * @return Decimal
	 */
	absoluteValue() {

		// Call the parent method and convert it
		return Decimal._fromDecimalJS(
			super.absoluteValue(),
			this.points
		);
	}

	/**
	 * Ceiling
	 *
	 * Round fractions up
	 *
	 * @name ceil
	 * @access public
	 * @return Decimal
	 */
	ceil() {

		// Call the parent method and convert it
		return Decimal._fromDecimalJS(
			super.ceil(),
			this.points
		);
	}

	/**
	 * Floor
	 *
	 * Round fractions down
	 *
	 * @name floor
	 * @access public
	 * @return Decimal
	 */
	floor() {

		// Call the parent method and convert it
		return Decimal._fromDecimalJS(
			super.floor(),
			this.points
		);
	}

	/**
	 * Minus
	 *
	 * Substracts two decimals
	 *
	 * @name minus
	 * @access public
	 * @param mixed x				The value to subtract from the current instance
	 * @return Decimal
	 */
	minus(x) {

		// Call the parent constructor and convert it
		return Decimal._fromDecimalJS(
			super.minus(x),
			this.points
		);
	}

	/**
	 * Negated
	 *
	 * Returns the decimal as a negative value
	 *
	 * @name negated
	 * @access public
	 * @return Decimal
	 */
	negated() {

		// Call the parent constructor and convert it
		return Decimal._fromDecimalJS(
			super.negated(),
			this.points
		);
	}

	/**
	 * Plus
	 *
	 * Adds two decimals
	 *
	 * @name plus
	 * @access public
	 * @param mixed x				The value to add to the current instance
	 * @return Decimal
	 */
	plus(x) {

		// Call the parent constructor and convert it
		return Decimal._fromDecimalJS(
			super.plus(x),
			this.points
		);
	}

	/**
	 * Round
	 *
	 * Rounds to the closest whole number
	 *
	 * @name round
	 * @access public
	 * @return Decimal
	 */
	round() {

		// Call the parent constructor and convert it
		return Decimal._fromDecimalJS(
			super.round(),
			this.points
		);
	}

	/**
	 * Times
	 *
	 * Multiplies two decimals
	 *
	 * @name times
	 * @access public
	 * @param mixed x				The value to multiply the current instance by
	 * @return Decimal
	 */
	times(x) {

		// Call the parent constructor and convert it
		return Decimal._fromDecimalJS(
			super.times(x),
			this.points
		);
	}

	/**
	 * To Decimal Places
	 *
	 * Rounds to the given decimal place
	 *
	 * @name toDecimalPlaces
	 * @access public
	 * @return Decimal
	 */
	toDecimalPlaces(dp, rm) {

		// Call the parent constructor and convert it
		return Decimal._fromDecimalJS(
			super.toDecimalPlaces(dp, rm),
			this.points
		);
	}

	/**
	 * To Nearest
	 *
	 * Rounds to the nearest multiple of x
	 *
	 * @name toNearest
	 * @access public
	 * @param mixed x				Value to check multiples of
	 * @return Decimal
	 */
	toNearest(x) {

		// Call the parent constructor and convert it
		return Decimal._fromDecimalJS(
			super.toNearest(x),
			this.points
		);
	}

	/**
	 * To Nearest
	 *
	 * Rounds to the nearest multiple of x
	 *
	 * @name toSignificantDigits
	 * @access public
	 * @param int sd				Significant digits
	 * @param int rm				Rounding mode
	 * @return Decimal
	 */
	toSignificantDigits(sd, rm) {

		// Call the parent constructor and convert it
		return Decimal._fromDecimalJS(
			super.toSignificantDigits(sd, rm),
			this.points
		);
	}

	/**
	 * To String
	 *
	 * Returns the decimal as a string representation
	 *
	 * @name toString
	 * @access public
	 * @returns String
	 */
	toString() {

		// Call the parent
		var s = super.toString();

		// If we have decimal points
		if(this.points) {

			// Get the current number from the return
			var a = types.regex.decimal.exec(s);

			// If it exists
			if(a) {

				// Start with the same length
				var len = this.points;

				// If we have a decimal point
				if(a[1]) {

					// If it's longer or equal to the the original
					if(a[1].length >= this.points) {
						return s;
					}

					// Get the difference
					len = this.points - a[1].length;
				}

				// Else, add the '.'
				else {
					s += '.';
				}

				// Add as many zeros and necessary
				for(var i = 0; i < len; ++i) {
					s += '0';
				}
			}
		}

		// Return the string
		return s;
	}

	/**
	 * Truncated
	 *
	 * Truncates the instance to a whole number
	 *
	 * @name truncated
	 * @access public
	 * @return Decimal
	 */
	truncated() {

		// Call the parent constructor and convert it
		return Decimal._fromDecimalJS(
			super.truncated(),
			this.points
		);
	}
}
