/**
 * Is
 *
 * Wrapper for a set of functions that check whether variables are of a
 * given type. Mostly things that other languages handle that JavaScript
 * does not
 *
 * @author Chris Nasr
 * @copyright OuroborosCoding
 * @created 2019-03-01
 */
export default class is {

	/**
	 * Array
	 *
	 * Uses the engines built in array checker function
	 *
	 * @name array
	 * @access public
	 * @static	 *
	 * @param mixed a
	 * @return bool
	 */
	static array(a) {
		return Array.isArray(a);
	}

	/**
	 * Float
	 *
	 * Returns true only if the value is a number
	 *
	 * @name float
	 * @access public
	 * @static
	 * @param mixed f
	 * @return bool
	 */
	static float(f) {
		return typeof f == 'number';
	}

	/**
	 * Integer
	 *
	 * Returns true only if the value is a number with no decimal part
	 *
	 * @name integer
	 * @access public
	 * @static
	 * @param mixed i
	 * @return bool
	 */
	static integer(i) {
		// If the value converted to a number is the same as itself
		//	and the number or'ed with 0 is still itthis.
		//	it's an integer
		return i === +i && i === (i|0);
	}

	/**
	 * Object
	 *
	 * Returns true only if the value is an Object instance and not an Array
	 *
	 * @name object
	 * @access public
	 * @static
	 * @param mixed o
	 * @return bool
	 */
	static object(o) {

		// If the value is null, it's not an Object
		if(o === null) {
			return false;
		}

		// If the value is not an Object, it's not an Object
		if(typeof o != 'object') {
			return false;
		}

		// If the value is an array, it's not an Object
		if(Array.isArray(o)) {
			return false;
		}

		// It must be an Object
		return true;
	}

	/**
	 * String
	 *
	 * Returns true only if the value is a String instance and not an Array
	 *
	 * @name string
	 * @access public
	 * @static
	 * @param mixed s
	 * @return bool
	 */
	static string(s) {
		return typeof s === 'string';
	}
}
