/**
 * Child
 *
 * Used to simplify the process of children in children in children
 *
 * @author Chris Nasr
 * @copyright OuroborosCoding
 * @created 2019-03-01
 */

// Import helpers
import is from './is';

// Private variables
const _classes = {};

/**
 * Create
 *
 * Figure out the child node type necessary and create an instance of it
 *
 * @name create
 * @access public
 * @static
 * @param object details			An object describing a data point
 * @return BaseNode
 */
function create(details) {

	// If it's an array, create a list of options
	if(is.array(details)) {
		return new _classes['options'](details);
	}

	// Else if we got an object
	else if(is.object(details)) {

		// If array is present
		if('__array__' in details) {
			return new _classes['array'](details);
		}

		// Else if we have a hash
		else if('__hash__' in details) {
			return new _classes['hash'](details);
		}

		// Else if we have a type
		else if('__type__' in details) {

			// If the type is an object or an array, this is a complex type
			if(is.object(details['__type__']) || is.array(details['__type__'])) {
				return create(details['__type__']);
			}

			// Else it's just a Node
			else {
				return new _classes['node'](details);
			}
		}

		// Else it's most likely a parent
		else {
			return new _classes['parent'](details)
		}
	}

	// Else if we got a string, use the value as the type
	else if(is.string(details)) {
		return new _classes['node'](details);
	}

	// Else, raise an error
	else {
		throw 'details invalid';
	}
}

/**
 * Register
 *
 * Registers the classes that can be children because we can't require them
 * in this file as webpack can't handle file A that requires file B that
 * requires file A
 *
 * @name register
 * @access public
 * @static
 * @param Object				Keys to classes
 * @return void
 */
function register(name, class_) {
	if(typeof name === 'object') {
		_classes = name;
	} else {
		_classes[name] = class_;
	}
}

// Export module
export default {
	create: create,
	register: register
}
