/**
 * Compare IPs
 *
 * Compares two IPs and returns a status based on which is greater
 * If first is less than second: -1
 * If first is equal to second: 0
 * If first is greater than second: 1
 *
 * @name compareIPs
 * @param String first				A string representing an IP address
 * @param String second				A string representing an IP address
 * @return int						-1, 0, 1
 */
export function CompareIPs(first, second) {

	// If the two IPs are the same, return 0
	if(first == second) {
		return 0;
	}

	// Create lists from the split of each IP, store them as ints
	var aFirst = first.split('.');
	for(var i in aFirst) { aFirst[i] = parseInt(aFirst[i]); }
	var aSecond = second.split('.');
	for(var i in aSecond) { aSecond[i] = parseInt(aSecond[i]); }

	// Go through each part from left to right until we find the
	// 	difference
	for(var i = 0; i < 4; ++i) {

		// If the part of x is greater than the part of y
		if(aFirst[i] > aSecond[i]) {
			return 1;
		}

		// Else if the part of x is less than the part of y
		else if(aFirst[i] < aSecond[i]) {
			return -1;
		}
	}
}

/**
 * Date to String
 *
 * Turns a Date Object into a date string
 *
 * @name DateToStr
 * @param Date date					The value to turn into a string
 * @return String
 */
export function DateToStr(date) {

	// Init the return variable
	var sRet = '';

	// Add the year
	sRet += date.getFullYear() + '-';

	// Add the month
	var iMonth = date.getMonth();
	sRet += ((iMonth < 10) ? '0' + iMonth : iMonth.toString()) + '-'

	// Add the day
	var iDay = date.getDate();
	sRet += ((iDay < 10) ? '0' + iDay : iDay.toString())

	// Return the date
	return sRet;
}

/**
 * DateTime to String
 *
 * Turns a Date Object into a date and time string
 *
 * @name DateTimeToStr
 * @param Date datetime				The value to turn into a string
 * @return String
 */
export function DateTimeToStr(datetime) {

	// Init the return variable with the date
	var sRet = DateToStr(datetime);

	// Add the time
	sRet +=  ' ' + datetime.toTimeString().substr(0,8);

	// Return the new date/time
	return sRet;
}

/**
 * String To Integer
 *
 * Converts a string to an int in hex, octal, or base10
 *
 * @name StrToInt
 * @param String s					The value to parse to an int
 * @param int
 */
export function StrToInt(s) {

	// If it starts with 0
	if(s.charAt(0) == '0' && s.length > 1) {

		// If it's followed by X or x, it's hex
		if(['x', 'X'].indexOf(s.charAt(1)) != -1 && s.length > 2) {
			return parseInt(s, 16)
		}

		// Else it's octal
		else {
			return parseInt(s, 8)
		}
	}

	// Else it's base 10
	else {
		return parseInt(s, 10);
	}
}

// Export the functions
export default {
	CompareIPs: CompareIPs,
	DateToStr: DateToStr,
	DateTimeToStr: DateTimeToStr,
	StrToInt: StrToInt
}
