/**
 * Array Node
 *
 * Represents a node which is actually an array containing lists of another node
 *
 * @author Chris Nasr
 * @copyright OuroborosCoding
 * @created 2019-03-01
 */

// Import modules
import BaseNode from './BaseNode';
import Child from './Child';

// Import helpers
import helpers from './helpers';
import is from './is.js';
import types from './types.js';

// ArrayNode class
export default class ArrayNode extends BaseNode {

	/**
	 * Constructor
	 *
	 * Initialises the instance
	 *
	 * @name ArrayNode
	 * @access public
	 * @param Object details			Node structure
	 * @returns ArrayNode
	 */
	constructor(details) {

		// If the details are not an Object
		if(!is.object(details)) {
			throw 'details must be an Object';
		}

		// If the array config is not found
		if(!('__array__' in details)) {
			throw 'missing "__array__" in details';
		}

		// If the value is not an Object
		if(!is.object(details['__array__'])) {
			details['__array__'] = {
				"type": details['__array__']
			}
		}

		// Call parent constructor
		super(details, 'ArrayNode');

		// Init the protected vars
		this._minimum = null;
		this._maximum = null;
		this._type = 'unique';
		this._node = null;

		// If the type is invalid
		if(types.array.indexOf(details['__array__']['type']) == -1) {
			console.error('"' + String(details['__array__']['type']) + '" is not a valid type for __array__, assuming "unique"');
		}

		// Else, store it
		else {
			this._type = details['__array__']['type'];
		}

		// If there's a minimum or maximum present
		if('minimum' in details['__array__'] || 'maximum' in details['__array__']) {
			this.minmax(
				('minimum' in details['__array__'] && details['__array__']['minimum'] || null),
				('maximum' in details['__array__'] && details['__array__']['maximum'] || null)
			);
		}

		// Remove it from details
		delete details['__array__'];

		// Store the child
		this._node = Child.create(details);
	}

	/**
	 * Child
	 *
	 * Returns the child node associated with the array
	 *
	 * @name child
	 * @access public
	 * @return object
	 */
	child() {
		return this._node;
	}

	/**
	 * Clean
	 *
	 * Goes through each of the values in the list, cleans it, stores it,
	 * and returns a new list
	 *
	 * @name clean
	 * @access public
	 * @param Array value				The value to validate
	 * @return Array
	 */
	clean(value) {

		// If the value is null and it's optional, return as is
		if((typeof value == 'undefined' || value == null) && this._optional) {
			return null;
		}

		// If the value is not an Array
		if(!is.array(value, list)) {
			throw 'value must be a valid Array';
		}

		// Recurse and return it
		var aRet = [];
		for(var i in value) {
			lRet.push(this._node.clean(value[i]));
		}
		return aRet;
	}

	/**
	 * Min/Max
	 *
	 * Sets or gets the minimum and/or maximum number of items for the
	 * Array. For getting, returns {"minimum":uint,"maximum":uint}
	 *
	 * @name minmax
	 * @access public
	 * @param uint minimum				The minimum value
	 * @param uint maximum				The maximum value
	 * @return void|Object
	 */
	minmax(minimum, maximum) {

		// If neither minimum or max is set, this is a getter
		if(typeof minimum === 'undefined' && typeof maximum === 'undefined') {
			return {"minimum":this._minimum,"maximum":this._maximum};
		}

		// If the minimum is set
		if(minimum !== null) {

			// If the value is not a valid int or long
			if(!is.integer(minimum)) {

				// If it's a valid representation of an integer
				if(is.string(minimum) && types.regex['int'].test(minimum)) {
					minimum = helpers.StrToInt(minimum);
				}

				// Else, raise an error
				else {
					throw '"minimum" of array must be an unsigned integer';
				}
			}

			// If it's below zero
			if(minimum < 0) {
				throw '"minimum" of array must be an unsigned integer';
			}

			// Store the minimum
			this._minimum = minimum;
		}

		// If the maximum is set
		if(maximum !== null) {

			// If the value is not a valid int or long
			if(!is.integer(maximum)) {

				// If it's a valid representation of an integer
				if(is.string(maximum) && types.regex['int'].test(maximum)) {
					maximum = helpers.StrToInt(maximum);
				}

				// Else, raise an error
				else {
					throw '"maximum" of array must be an unsigned integer';
				}
			}

			// If it's below zero
			if(maximum < 0) {
				throw '"maximum" of array must be an unsigned integer';
			}

			// If we also have a minimum and the maximum is somehow below it
			if(this._minimum && maximum < this._minimum) {
				throw '"maximum" of array must not be less than "minimum"';
			}

			// Store the maximum
			this._maximum = maximum;
		}
	}

	/**
	 * To Object
	 *
	 * Returns the Array as an Object in the same format as is used in
	 * constructing it
	 *
	 * @name toObject
	 * @access public
	 * @return Object
	 */
	toObject() {

		// Init the object we will return
		var oRet = {};

		// If either a min or a max is set
		if(this._minimum || this._maximum) {

			// Set the array element as it's own Object
			oRet['__array__'] = {
				"type": this._type
			}

			// If there is a minimum
			if(this._minimum) {
				oRet['__array__']['minimum'] = this._minimum;
			}

			// If there is a maximum
			if(this._maximum) {
				oRet['__array__']['maximum'] = this._maximum;
			}
		}

		// Else, just add the type as the array element
		else {
			oRet['__array__'] = this._type;
		}

		// Get the parents Object and add it to the return
		var oBasic = super.toObject();
		for(var k in oBasic) {
			oRet[k] = oBasic[k];
		}

		// Get the nodes Object and also add it to the return
		var oNode = this._node.toObject();
		for(var k in oNode) {
			oRet[k] = oNode[k];
		}

		// Return
		return oRet;
	}

	/**
	 * Type
	 *
	 * Returns the type of array, unique or duplicate
	 *
	 * @name type
	 * @access public
	 * @return String
	 */
	type() {
		return this._type;
	}

	/**
	 * Valid
	 *
	 * Checks if a value is valid based on the instance's values
	 *
	 * @name valid
	 * @access public
	 * @param mixed value				The value to validate
	 * @param array level				Names of parents to this node
	 * @return bool
	 */
	valid(value, level) {

		// Reset validation failures
		this.validation_failures = []

		// If level is not passed
		if(typeof level == 'undefined') {
			level = [];
		}

		// If the value is null and it's optional, we're good
		if(value == null && this._optional) {
			return true;
		}

		// If the value isn't an array
		if(!is.array(value)) {
			this.validation_failures.push([level.join('.'), String(value)]);
			return false;
		}

		// Init the return, assume valid
		var bRet = true;

		// Keep track of duplicates
		if(this._type == 'unique') {
			var aItems = [];
		}

		// Go through each item in the list
		for(var i = 0; i < value.length; ++i) {

			// Add the field to the level
			var lLevel = level.slice(0);
			lLevel.push('[' + i + ']');

			// If the element isn't valid, return false
			if(!this._node.valid(value[i], lLevel)) {
				this.validation_failures = this.validation_failures.concat(this._node.validation_failures.splice(0));
				bRet = false;
				continue;
			}

			// If we need to check for duplicates
			if(this._type == 'unique') {

				// Look for the value in the existing list
				var iIndex = aItems.indexOf(value[i]);

				// If it's found, we have a duplicate
				if(iIndex > -1) {

					// Add the error to the list
					this.validation_failures.push([lLevel.join('.'), 'duplicate of ' + level.join('.') + '[' + iIndex + ']']);
					bRet = false;
					continue;
				}

				// Else, we can add the value
				aItems.push(value[i]);
			}
		}

		// If there's a minumum
		if(this._minimum != null) {

			// If we don't have enough
			if(value.length < this._minimum) {
				this.validation_failures.push([level.join('.'), 'did not meet minimum']);
				bRet = false;
			}
		}

		// If there's a maximum
		if(this._maximum != null) {

			// If we have too many
			if(value.length > this._maximum) {
				this.validation_failures.push([level.join('.'), 'exceeds maximum']);
				bRet = false;
			}
		}

		// Return ok
		return bRet;
	}
}

// Register with Child
Child.register('array', ArrayNode);
