/**
 * Hash Node
 *
 * Represents a node which is actually an object containing defined keys (Node)
 * and values (BaseNode)
 *
 * @author Chris Nasr
 * @copyright OuroborosCoding
 * @created 2019-03-01
 */

// Import modules
import BaseNode from './BaseNode';
import Child from './Child';
import Node from './Node';

// Import helpers
import is from './is';
import types from './types';

// HashNode class
export default class HashNode extends BaseNode {

	/**
	 * Constructor
	 *
	 * Initialises the instance
	 *
	 * @name HashNode
	 * @access public
	 * @param Object details			Node structure
	 * @returns HashNode
	 */
	constructor(details) {

		// If the details are not an Object
		if(!is.object(details)) {
			throw 'details must be an Object';
		}

		// If the hash config is not found
		if(!('__hash__' in details)) {
			throw 'missing "__hash__" in details';
		}

		// Call the parent constructor
		super(details, 'HashNode');

		// If the hash is simply set to true, make it a string with no
		//	requirements
		if(details['__hash__'] == true) {
			details['__hash__'] = {'__type__': 'string'};
		}

		// Store the key using the hash value
		this._key = new Node(details['__hash__']);

		// Remove it from details
		delete details['__hash__'];

		// Store the child
		this._node = Child.create(details);
	}

	/**
	 * Child
	 *
	 * Returns the child node associated with the hash
	 *
	 * @name child
	 * @access public
	 * @return object
	 */
	child() {
		return this._node;
	}

	/**
	 * Clean
	 *
	 * Makes sure both the key and value are properly stored in their
	 * correct representation
	 *
	 * @name clean
	 * @access public
	 * @param mixed value				The value to clean
	 * @return mixed
	 */
	clean(value) {

		// If the value is null and it's optional, return as is
		if(value == null && this._optional) {
			return null;
		}

		// If the value is not an Object
		if(!is.object(value)) {
			throw 'value must be a valid Object';
		}

		// Recurse and return it
		var oRet = {};
		for(var k in value) {
			oRet[this._key.clean(k)] = this._node.clean(value[k]);
		}
		return oRet;
	};

	/**
	 * To Object
	 *
	 * Returns the basic node as a dictionary in the same format as is used
	 * in constructing it
	 *
	 * @name toObject
	 * @access public
	 * @return Object
	 */
	toObject() {

		// Init the object we will return
		var oRet = {};

		// Add the hash key
		oRet['__hash__'] = this._key.toObject();

		// Get the parents Object and add it to the return
		var oBase = super.toObject();
		for(var k in oBase) {
			oRet[k] = oBase[k];
		}

		// Get the nodes Object and also add it to the return
		var oNode = this._node.toObject();
		for(var k in oNode) {
			oRet[k] = oNode[k];
		}

		// Return
		return oRet;
	}

	/**
	 * Valid
	 *
	 * Checks if a value is valid based on the instance's values
	 *
	 * @name valid
	 * @access public
	 * @param mixed value			The value to validate
	 * @param array level			Names of parents to this node
	 * @return bool
	 */
	valid(value, level) {

		// Reset validation failures
		this.validation_failures = [];

		// If the value is null and it's optional. we're good
		if(value == null && this._optional) {
			return true;
		}

		// If level is not passed
		if(typeof level == 'undefined') {
			level = [];
		}

		// If the value isn't an Object
		if(!is.object(value)) {
			this.validation_failures.push([level.join('.'), String(value)]);
			return false;
		}

		// Init the return, assume valid
		var bRet = true;

		// Go through each key
		for(var k in value) {

			// Add the field to the level
			var lLevel = level.slice(0);
			lLevel.push(k);

			// If the key isn't valid
			if(!this._key.valid(String(k))) {
				this.validation_failures.push([lLevel.join('.'), 'invalid key: ' + String(k)]);
				bRet = false;
				continue;
			}

			// Check the value
			if(!this._node.valid(value[k], lLevel)) {
				this.validation_failures = this.validation_failures.concat(this._node.validation_failures.splice(0));
				bRet = false;
				continue;
			}
		}

		// Return whatever the result was
		return bRet;
	}
}

// Register with Child
Child.register('hash', HashNode);
