/**
 * Parent
 *
 * Represents defined keys mapped to other Nodes which themselves could be
 * Parents
 *
 * @author Chris Nasr
 * @copyright OuroborosCoding
 * @created 2019-03-01
 */

// Import modules
import Parent from './Parent';

// Import helpers
import is from './is';
import types from './types';

// Tree class
export default class Tree extends Parent {

	/**
	 * Constructor
	 *
	 * Initialises the instance
	 *
	 * @name Tree
	 * @access public
	 * @param Object details			Node structure
	 * @returns Tree
	 */
	constructor(details) {

		// If the details are not an Object
		if(!is.object(details)) {
			throw 'details must be an Object';
		}

		// If the name is not set
		if(!('__name__' in details)) {
			throw '"__name__" not found in details';
		}

		// If the name is not valid
		if(!types.standard.test(details['__name__'])) {
			throw '"__name__" not a valid value for Tree';
		}

		// If for some reason the array flag is set, remove it
		if('__array__' in details) {
			delete details['__array__'];
		}

		// Call the parent constructor
		super(details);

		// Store the name then delete it
		this._name = details['__name__'];

		// Overwrite classname
		this._class = 'Tree';
	}

	/**
	 * Class
	 *
	 * Returns a string representation of the Node's class
	 *
	 * @name class
	 * @access public
	 * @return String
	 */
	class() {
		return 'Tree';
	}

	/**
	 * To Object
	 *
	 * Returns the Node as a dictionary in the same format as is used in
	 * constructing it
	 *
	 * @name toObject
	 * @access public
	 * @return Object
	 */
	toObject() {

		// Init the Object we will return
		var oRet = {
			"__name__": this._name
		}

		// Get the parents dict and add it to the return
		var oParent = super.toObject();
		for(var k in oParent) {
			oRet[k] = oParent[k];
		}

		// Return
		return oRet;
	}

	/**
	 * Valid
	 *
	 * Checks if the passed dict has the right values for this Parent
	 *
	 * @name valid
	 * @param Object value				The value to validate
	 * @return bool
	 */
	valid(value, include_name) {

		// If the include_name param wasn't sent, assume true
		if(typeof include_name == 'undefined') {
			include_name = true;
		}

		// Call the Parent valid method
		return super.valid(value, (include_name ? [this._name] : []));
	}
}
