/**
 * Options Node
 *
 * Represents a node which can have several different types of values/Nodes and
 * still be valid
 *
 * @author Chris Nasr
 * @copyright OuroborosCoding
 * @created 2019-03-01
 */

// Import modules
import Child from './Child';

// Import helpers
import is from './is';

// Node class
export default class OptionsNode {

	/**
	 * Constructor
	 *
	 * Initialises the instance
	 *
	 * @name OptionsNode
	 * @access public
	 * @param Array details				Node structure
	 * @returns OptionsNode
	 */
	constructor(details) {

		// If the details are not an Array
		if(!is.array(details)) {
			throw 'details must be an Array';
		}

		// Init the variable used to identify the last falures in validation
		this.validation_failures = [];

		// Init the optional flag, assume all nodes are optional until we find
		//	one that isn't
		this._optional = true;

		// Init the internal list
		this._nodes = [];

		// Go through each element in the list
		for(var i = 0; i < details.length; ++i) {

			// Add the child
			this._nodes.push(Child.create(details[i]))

			// If the child is not optional, than the entire object can't be
			//	optional
			if(!this._nodes[i].optional()) {
				this._optional = false;
			}
		}
	}

	/**
	 * Class
	 *
	 * Returns a string representation of the Node's class
	 *
	 * @name class
	 * @access public
	 * @return String
	 */
	class() {
		return 'OptionsNode';
	}

	/**
	 * Clean
	 *
	 * Uses the valid method to check which type the value is, and then
	 * calls the correct version of clean on that node
	 *
	 * @name clean
	 * @access public
	 * @param mixed value				The value to clean
	 * @return mixed
	 */
	clean(value) {

		// If the value is null and it's optional, return as is
		if(value == null && this._optional) {
			return null;
		}

		// Go through each of the nodes
		for(var i = 0; i < this._nodes.length; ++i) {

			// If it's valid
			if(this._nodes[i].valid(value)) {

				// Use its clean
				return this._nodes[i].clean(value);
			}
		}

		// Something went wrong
		throw 'invalid value';
	}

	/**
	 * Optional
	 *
	 * Returns if the Node is optional or not
	 *
	 * @name optional
	 * @return bool
	 */
	optional() {
		return this._optional;
	}

	/**
	 * To JSON
	 *
	 * Returns a JSON string representation of the instance
	 *
	 * @name toJSON
	 * @access public
	 * @return String
	 */
	toJSON() {
		return JSON.stringify(this.toObject());
	}

	/**
	 * To Object
	 *
	 * Returns the Nodes as a list of dictionaries in the same format as is used
	 * in constructing them
	 *
	 * @name toObject
	 * @access public
	 * @return Array
	 */
	toObject() {

		// Init the Array
		var aRet = [];

		// Go through each of the nodes
		for(var i = 0; i < this._nodes.length; ++i) {
			aRet.push(this._nodes[i].toObject());
		}

		// Return the Array
		return aRet;
	}

	/**
	 * Valid
	 *
	 * Checks if a value is valid based on the instance's values
	 *
	 * @name valid
	 * @access public
	 * @param mixed value				The value to validate
	 * @return bool
	 */
	valid(value, level) {

		// Reset validation failures
		this.validation_failures = [];

		// If the value is None and it's optional, we're good
		if(value == null && this._optional) {
			return true;
		}

		// If level is not passed
		if(typeof level == 'undefined') {
			level = [];
		}

		// Go through each of the nodes
		for(var i = 0; i < this._nodes.length; ++i) {

			// If it's valid
			if(this._nodes[i].valid(value)) {
				return true;
			}
		}

		// Not valid for anything
		this.validation_failures.push([level.join('.'), 'no valid option']);
		return false;
	}
}

// Register with Child
Child.register('options', OptionsNode);
