// Import modules
import ArrayNode from'./ArrayNode';
import Decimal from'./Decimal';
import HashNode from'./HashNode';
import Node from'./Node';
import OptionsNode from'./OptionsNode';
import Parent from'./Parent';
import Tree from'./Tree';
import types from './types';

// Generate the object of classes
const FormatOC = {
	_Decimal: Decimal,
	ArrayNode: ArrayNode,
	HashNode: HashNode,
	Node: Node,
	OptionsNode: OptionsNode,
	Parent: Parent,
	Tree: Tree,
	types: types
};

// Export the classes
export default FormatOC;

// If window exists
if(window) {
	window.FormatOC = FormatOC;
}
